@use '../../utilities/variables';

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 250px;

    position: sticky;
    top: 0;
    align-self: flex-start;

    h1#nav-head {
        margin: 2rem 0;
    }

    nav#nav-bar {
        ol {
            padding: 0;
            list-style-type: none;
            
            li {
                &:first-of-type {
                    margin-bottom: 1rem;
                }

                &:hover {
                    color: variables.$silver-lake-blue;
                }

                a {
                    color: inherit;
                    text-decoration: none;
                }
            }
        }
    }
}